$col-one: #fef5ed;
.cursor {
  position: fixed;
  background-color: black;
  width: 15px;
  height: 15px;
  border-radius: 100%;
  z-index: 5;
  transition: 0.3s cubic-bezier(0.75, -1.27, 0.3, 2.33) transform,
    0.2s cubic-bezier(0.75, -0.27, 0.3, 1.33) opacity;
  user-select: none;
  pointer-events: none;
  z-index: 10000;
  transform: scale(1);
}
.App {
  cursor: none;
}
